import './App.css';
import React, { useEffect, useState } from "react";
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { BrowserRouter, Routes, Route, NavLink, Navigate } from "react-router-dom";
import Accueil from './components/Accueil';
import LogIn from './components/LogIn';
import SignUp from './components/SignUp';
import LogOut from './components/LogOut';
import Calendrier from './components/Calendrier';
import Axios from 'axios';

const App = () => {
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [currentUserId, setCurrentUserId] = useState(-1);
  const [currentUserName, setCurrentUserName] = useState("");
  const [currentUser, setCurrentUser] = useState({ id: "", username: "", password: "" });

  Axios.defaults.withCredentials = true;

  const logIn = (username, password) => {
    var error = Axios.post("http://localhost:8081/login", {
      username: username,
      password: password,
    }, { withCredentials: true }).then((response) => {
      if (response.data.message) {
        return response.data.message;
      } else {
        console.log(response.data[0].username);
        setCurrentUser(response.data[0]);

        console.log("Cookies" + document.cookie);
      }
    });

    return error;
  };

  const register = (username, password) => {
    var error = Axios.post("http://localhost:8081/register", {
      username: username,
      password: password,
    }, { withCredentials: true })
      .then((response) => {
        return response.data.message ? response.data.message : "";
      });

    return error;
  }

  const logOut = () => {
    Axios.post("http://localhost:8081/logout", {}, { withCredentials: true })
      .then((response) => {
        console.log(response.status);
        setCurrentUser({ id: "", username: "", password: "" });
        console.log('Cookie :' + document.cookie);
      });
  }

  const getLogIn = () => {
    Axios.get("http://localhost:8081/login", {}, { withCredentials: true }).then((response) => {
      console.log(response.data.loggedIn);
      setIsLoggedIn(response.data.loggedIn);
      response.data.loggedIn ? setCurrentUserId(response.data.user[0].id) : setCurrentUserId(-1);
      response.data.loggedIn ? setCurrentUserName(response.data.user[0].username) : setCurrentUserName("");
      console.log('CurrentUser: ' + currentUser.username);
    });
  }

  useEffect(() => {
    getLogIn();
  }, [currentUser]);

  return (
    <div className="App">
      <BrowserRouter>
        <Navbar bg="dark" variant="dark">
          <Nav className="me-auto">
            <NavLink to="/" end={true} className="navLink" style={({ isActive }) => ({ backgroundColor: isActive ? 'white' : null })}>Accueil</NavLink>
            <NavLink to="/logIn" className="navLink" style={({ isActive }) => ({ backgroundColor: isActive ? 'white' : null })} hidden={isLoggedIn}>Se connecter</NavLink>
            <NavLink to="/signUp" className="navLink" style={({ isActive }) => ({ backgroundColor: isActive ? 'white' : null })} hidden={isLoggedIn}>S'inscrire</NavLink>
            <NavLink to="/calendrier" className="navLink" style={({ isActive }) => ({ backgroundColor: isActive ? 'white' : null })} hidden={!isLoggedIn}>Calendrier</NavLink>
          </Nav>
          <Nav className='ms-auto'>
            <NavLink to="/logOut" className="navLink" style={({ isActive }) => ({ backgroundColor: isActive ? 'white' : null })} hidden={!isLoggedIn}>Se déconnecter</NavLink>
            <label className="navLink">{currentUserName}</label>
          </Nav>
        </Navbar>
        <Container className='container-fluid'>
          <Container className='justify-content-md-center row'>
            <Container className='col-sm-7'>
              <Routes>
                <Route path="/" element={<Accueil />} />
                <Route path="/logIn" element={!isLoggedIn ? <LogIn logIn={logIn} /> : <Navigate to="/" />} />
                <Route path="/signUp" element={!isLoggedIn ? <SignUp register={register} logIn={logIn} /> : <Navigate to="/" />} />
                <Route path="/logOut" element={isLoggedIn ? <LogOut logOut={logOut} /> : <Navigate to="/" />} />
                <Route path="/calendrier" element={isLoggedIn ? <Calendrier userId={currentUserId} /> : <Navigate to="/" />} />
              </Routes>
            </Container>
          </Container>
        </Container>

      </BrowserRouter>
    </div>
  );
}

export default App;
