import React, { useState } from 'react';
import Container from 'react-bootstrap/esm/Container';
import FullCalendar from '@fullcalendar/react';
import DayGridPlugin from '@fullcalendar/daygrid';
import InteractionPlugin from '@fullcalendar/interaction';
import Axios from 'axios';

const Calendrier = (props) => {
  const forceUpdate = useForceUpdate();

  Axios.defaults.withCredentials = true;

  const getEvents = (info, successCallback, failureCallback) => {
    Axios.get("http://localhost:8081/getEventsByUserId?userId=" + props.userId, {}, { withCredentials: true })
      .then((res) => {
        successCallback(
          res.data.map((event) => {
            return {
              title: event.title,
              date: event.date,
              id: event.id
            }
          })
        );
      });
  };

  const addEvent = (title, date) => {
    Axios.post("http://localhost:8081/addEvent", {
      title: title,
      date: date,
      userId: props.userId
    }, { withCredentials: true })
      .then((res) => {
        console.log(res.status);
        forceUpdate();
      });
  }

  const deleteEvent = (eventId) => {
    Axios.delete("http://localhost:8081/deleteEvent", {
      data: { id: eventId }
    }, { withCredentials: true })
      .then((res) => {
        console.log(res.status);
        forceUpdate();
      });
  }

  const handleDateSelect = (selectInfo) => {
    let title = prompt('Veuillez entrer un nouveau titre pour votre évenement');

    if (title) {
      addEvent(title, selectInfo.startStr);
    }
  }

  const handleEventClick = (clickInfo) => {
    if (window.confirm(`Êtes-vous certain de vouloir supprimer l'évenement?'${clickInfo.event.title}'`)) {
      deleteEvent(clickInfo.event.id);
    }
  }

  return (
    <Container>
      <h1>Calendrier</h1>
      <FullCalendar
        locale='fr'
        plugins={[DayGridPlugin, InteractionPlugin]}
        initialView='dayGridMonth'
        events={getEvents}
        selectable={true}
        select={handleDateSelect}
        eventClick={handleEventClick} />
    </Container>
  );
};

/**
 * Utilitary component to force a re-render
 */
const useForceUpdate = () => {
  const [value, setValue] = useState(0);
  return () => setValue(() => value + 1);
}

export default Calendrier;