import React, { useState } from 'react';

const SignUp = (props) => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [errorMessage, setErrorMessage] = useState("");

    return (
        <div>
            <p style={{ color: 'red' }}>{errorMessage}</p>
            <div>
                <p>Nom d'utilisateur: </p>
                <input onChange={(e) => setUsername(e.target.value)} value={username}></input>
            </div>
            <br/>
            <div>
                <p>Mot de passe: </p>
                <input onChange={(e) => setPassword(e.target.value)} value={password}></input>
            </div>
            <br/>
            <button onClick={() => {
                if (username.length < 6) {
                    setErrorMessage("Le nom d'utilisateur doit être d'au moin 6 caractères");
                } else if(password.length < 6) {
                    setErrorMessage("Le mot de passe doit être d'au moin 6 caractères");
                } else {
                    (props.register(username, password).then(res => {
                        console.log(res)
                        if(res !== ""){
                            setErrorMessage(res)
                        } else {
                            props.logIn(username, password);
                        }
                    }));
                }
            }}>
                S'inscrire
            </button>
        </div>
    );
};

export default SignUp;
