import React, { useState } from 'react';

const LogIn = (props) => {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [errorMessage, setErrorMessage] = useState("");
    console.log(window.location.hostname);
    return (
        <div>
            <p style={{color: 'red'}}>{errorMessage}</p>
            <div>
                <p>Nom d'utilisateur: </p>
                <input onChange={(e) => setUsername(e.target.value)} value={username}></input>
            </div>
            <br/>
            <div>
                <p>Mot de passe: </p>
                <input onChange={(e) => setPassword(e.target.value)} value={password}></input>
            </div>
            <br/>
            <button onClick={() => (props.logIn(username, password).then(res => setErrorMessage(res)))}>Log in</button>
        </div>
    );
};

export default LogIn;